import {profileAPI} from '../api/api';

const ADD_POST = 'ADD-POST';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET-STATUS';
const REMOVE_POST = 'REMOVE-POST';

const initialState = {
    profile: null,
    posts: [
        {id: 1, message: 'Hi, how are you?', likesCount: 9},
        {id: 2, message: 'It\'s my first post', likesCount: 4},
    ],
    isFetching: false,
    status: ''
};

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, {
                    id: 5,
                    message: action.formData.newPostBody,
                    likesCount: 0
                }]
            };
        case REMOVE_POST:
            return {
                ...state,
                posts: state.posts.filter((el) => {
                    return el.id !== action.id;
                })
            };
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            };
        default:
            return state;
    }
};

export const addPost = (formData) => ({type: ADD_POST, formData});
export const removePost = (id) => ({type: REMOVE_POST, id});
const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
const setStatus = (status) => ({type: SET_STATUS, status});


export const getProfile = (userId) => async (dispatch) => {
    dispatch(toggleIsFetching(true));
    const response = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(response.data));
    dispatch(toggleIsFetching(false));
};

export const getStatus = (userId) => async (dispatch) => {
    const response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response.data));
};

export const updateStatus = status => async (dispatch) => {
    const response = await profileAPI.updateStatus(status);
    !response.data.resultCode && dispatch(setStatus(status));
};

export default profileReducer;