const SEND_MESSAGE = 'SEND-MESSAGE';
const initialState = {
    dialogs: [
        {id: 1, name: 'Stas'},
        {id: 2, name: 'Yulia'},
        {id: 3, name: 'Sasha'},
        {id: 4, name: 'Igor'},
        {id: 5, name: 'Alex'},
        {id: 6, name: 'Roma'},
    ],
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'Hi-hi'},
        {id: 3, message: 'How is your it-kamasutra?'},
    ]
};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            return {
                ...state,
                messages: [...state.messages, {id: 6, message: action.formData.newText}]
            };
        default:
            return state;
    }
};

export const sendMessage = (formData) => ({type: SEND_MESSAGE, formData});

export default dialogsReducer;