import {usersAPI} from '../api/api';
import {updateObjectInArray} from '../utils/helpers/object-helpers';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_FOLLOWING_PROGRESS = 'TOGGLE-FOLLOWING-PROGRESS';

const initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    isFollowingInProgress: []
};

const toggleFollowed = (state, userId, isFollow) => ({
    ...state,
    users: updateObjectInArray(state.users, userId, 'id', {followed: isFollow}),
});

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return toggleFollowed(state, action.userId, false);
        case UNFOLLOW:
            return toggleFollowed(state, action.userId, true);
        case SET_USERS:
            return {
                ...state,
                users: [...action.users]
            };
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.pageNum
            };
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalCount
            };
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            };
        case TOGGLE_FOLLOWING_PROGRESS:
            return {
                ...state,
                isFollowingInProgress: action.isFetching
                    ? [...state.isFollowingInProgress, action.userId]
                    : state.isFollowingInProgress.filter(id => id !== action.userId)
            };
        default:
            return state;
    }
};

const setUsers = (users) => ({type: SET_USERS, users});
const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USERS_COUNT, totalCount});
const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});

export const setCurrentPage = (pageNum) => ({type: SET_CURRENT_PAGE, pageNum});
export const toggleFollowingProgress = (userId, isFetching) => ({
    type: TOGGLE_FOLLOWING_PROGRESS,
    isFetching,
    userId
});
const onFollow = (userId) => ({type: FOLLOW, userId});
const onUnfollow = (userId) => ({type: UNFOLLOW, userId});


export const getUsersThunkCreator = (pageSize, currentPage) =>
    async (dispatch) => {
        dispatch(toggleIsFetching(true));
        const response = await usersAPI.getUsers(pageSize, currentPage);
        dispatch(toggleIsFetching(false));
        dispatch(setTotalUsersCount(response.totalCount));
        dispatch(setUsers(response.items));
    };

export const toggleFollow = (userId, isFollow) =>
    async (dispatch) => {
        dispatch(toggleFollowingProgress(userId, true));
        const response = await usersAPI[isFollow ? 'unfollow' : 'follow'](userId);
        if (response.data.resultCode === 0) dispatch(isFollow ? onFollow(userId) : onUnfollow(userId));
        dispatch(toggleFollowingProgress(userId, false));
    };

export default usersReducer;