import {getAuthUserData} from './auth-reducer';

const SET_IS_INITIALIZED = 'SET-IS-INITIALIZED';

const initialState = {
    isInitialized: false,
};

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_IS_INITIALIZED:
            return {
                ...state,
                isInitialized: true
            };
        default:
            return state;
    }
};

/*  Action Creators  */
const setIsInitialized = () => ({type: SET_IS_INITIALIZED});

/*  Thunks  */
const initializeApp = () => dispatch => {
    const promise = dispatch(getAuthUserData());

    Promise.all([promise])
        .then((response) => {
            dispatch(setIsInitialized(response));
        });
};

/*  Exports  */
export {initializeApp};
export default appReducer;