import {authAPI} from '../api/api';
import {stopSubmit} from 'redux-form';

const SET_USER_DATA = 'SET-USER-DATA';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const SET_CAPTCHA = 'SET-CAPTCHA';

const initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    isFetching: false,
    captcha: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data
            };
        case SET_CAPTCHA:
            return {
                ...state,
                captcha: action.url
            };
        default:
            return state;
    }
};

/* Action Creators */

const setAuthUserData = (userId, email, login, isAuth) => ({type: SET_USER_DATA, data: {userId, email, login, isAuth}});
const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
const setCaptcha = (url) => ({type: SET_CAPTCHA, url});


/* Thanks */

const getAuthUserData = () => (dispatch) => {
    dispatch(toggleIsFetching(true));
    return new Promise(async (resolve) => {
        const response = await authAPI.getMe();
        dispatch(toggleIsFetching(false));
        if (response.resultCode === 0) {
            const {id, email, login} = response.data;
            dispatch(setAuthUserData(id, email, login, true));
        }
        return resolve();
    });
};

const login = (loginData) =>
    async (dispatch) => {
        const response = await authAPI.login(loginData);

        if (response.data.resultCode === 0) {
            dispatch(getAuthUserData());
            dispatch(setCaptcha(null));
        } else if (response.data.resultCode === 1) {
            const action = stopSubmit('login', {_error: {invalid: response.data.messages.toString()}});
            dispatch(action);
            setCaptcha(null);
        } else if (response.data.resultCode === 10) {
            const action = stopSubmit('login', {_error: {invalid: response.data.messages.toString()}});
            authAPI.getCaptcha().then(response => {
                dispatch(setCaptcha(response.data.url));
                dispatch(action);
            });
        }
    };

const logout = () => async (dispatch) => {
    const response = await authAPI.logout();
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
};


/* Exports */
export {getAuthUserData, login, logout, setCaptcha};
export default authReducer;