import React from 'react';
import s from './NavFriends.module.scss';
import {Link} from 'react-router-dom';

const NavFriends = (props) => {
    const friendsData = props.friends;

    return (
        <section className={s.wrapper}>
            <Link to="/friends" className={s.title}>Friends</Link>
            <div className={s.list}>
                {friendsData.map(friend => {
                    return (
                        <Link to={`/friends/${friend.id}`} className={s.item} key={friend.id}>
                            <img className={s.itemPic} src={friend.pic} alt=""/>
                            <div className={s.itemName}>{friend.name}</div>
                        </Link>
                    );
                })}
            </div>
        </section>
    );
};

export default NavFriends;